import React from "react";
import { Link } from "react-router-dom";
import "./document.css";
// 文档页面
export default class Document extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "文档",
      list: [
        {
          name: "react基础学习",
          path: "/react-basic"
        },
        {
          name: "路由学习",
          path: "/router"
        },        
        {
          name: "效果",
          path: "/wirkung"
        }
      ]
    };
  }
  render() {
    return (
      <div className="Document App_main">
        <div className="common_header">
          <Link
            className="App-link"
            to="/"
            style={{ position: "absolute", left: 0, top: "10px" }}
          >
            {" "}
            &lt;--回到首页
          </Link>
          {/* <a className="App-link" href="#/" target="_self" rel="noopener noreferrer">回到主页></a> */}
          <div className="common_title">{this.state.title}</div>
          <hr />
        </div>
        <div className="common_content">
          <ul>
            {this.state.list.map((v,index) => (
              <li key={index}>
                <Link className="App-link" to={v.path}>
                  {index+1} {v.name}
                </Link>
              </li>
            ))}
          </ul>
        </div>
      </div>
    );
  }
}
