// react
import React from 'react';
import { renderRoutes } from 'react-router-config' //引入多级路由

export default class ReactKnowledge extends React.Component {
    render() {
        const {location,route} = this.props;
        return (
            <div>
               {location.pathname==='/wirkung/to_reactknowledge'?(<div>
                   <h2>React知识点</h2>
                  </div>):(renderRoutes(route.children))}
            </div>
        )
    }
}