// 父子如何传值
import React from 'react';

import { Link } from 'react-router-dom'
import ChildComponent from '../child2/child-component'
import ChildSearch from '../child2/child-search'
import ChildLabel from '../child2/child-label'
export default class PassByValue extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'父组件页面',
        sonInputValue:'123',
        brotherValue:'123',
        searchValue:'123456',
        searchValue2:''
      }
    }
    // 获取子组件信息
    getSonInfo(){
      console.log(this.refs.ChildComponent)
      this.refs.ChildComponent.state.value='父将子值改变'
    }
    // 传给子的方法
    changeInputValue(value){
      console.log(value) //子传的值
      this.setState({
        sonInputValue:value
      })
    }

    // 兄弟传值
    searchChange(searchValue){
      console.log('兄弟组件A：',searchValue)
      this.setState({
        searchValue
      })
    } 
    // 兄弟传值2
    searchChange2(searchValue2){
      this.setState({
        searchValue2
      })
    }
    render() {
        return (
            <div className="PassByValue App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--回到文档页
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">                     
                     <div style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                          <div>
                              <input type='button' value={'获取子组件属性或方法'} onClick={this.getSonInfo.bind(this)}/>
                          </div>
                          <br/>
                          <ChildComponent ref="ChildComponent" fatherInfo={'父组件传值-父传来一声问候'} sonInputValue={this.state.sonInputValue} changeInputValue={this.changeInputValue.bind(this)}/>
                          <br/>
                          <div>兄弟之间传值：</div>
                            <ChildSearch ref="ChildSearch" searchValue={this.state.searchValue}  searchChange={this.searchChange.bind(this)} searchChange2={this.searchChange2.bind(this)}/>
                            <ChildLabel ref="ChildLabel" searchValue={this.state.searchValue} searchValue2={this.state.searchValue2}/>
                          <br/>
                          <br/>
                          <h3>总结：</h3>
                          <div style={{whiteSpace:'pre'}}>
                              父传子属性： <br/>
                              例：子组件{`<ChildComponent  fatherInfo={'父组件传值-父传来一声问候'} />`}<br/>
                              父通过给子组件属性传入 fatherInfo属性,子组件 super(props)必写，props有父组件传入的属性，子组件使用方式 this.props.fatherInfo<br/><br/>
                              父传子方法（相当于子传父值）：<br/>
                              例：子组件{`<ChildComponent  fatherInputChange={this.fatherInputChange.bind(this)} />`}<br/>
                              父通过给子组件属性传入  fatherInputChange方法,子组件 super(props)必写，props有父组件传入的方法，子组件使用方式 this.props.fatherInputChange(参数1,参数2,参数3)<br/>
                              则父这边fatherInputChange可以接收子的参数,然后通过 this.setState更新值<br/><br/>
                              父使用子属性方法：
                              例：子组件{`<ChildComponent ref='ChildComponent' /> `}<br/>
                              父通过 this.refs.ChildComponent使用<br/><br/>
                              兄弟传值：<br/>
                              通过将属性在父组件定义，传值props传值给子组件，子组件通过 this.props.searchChange(e.target.value) 传值给父，父再讲setState更新属性

                          </div>
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
      console.log('searchA',this.refs.ChildSearch.state.searchA)
      this.setState({
        searchValue2:this.refs.ChildSearch.state.searchA
      })
    }
    componentWillUnmount() {
      // 卸载异步操作设置状态
      this.setState = (state, callback) => {
        return
      }
    }
}