// 路由子路由配置
import React from 'react'
import { HashRouter } from 'react-router-dom'
import { renderRoutes } from 'react-router-config';

import App from '../../App'
import Document from '../../views/Document'
import ReactBasic from '../../views/ReactBasic'
import PropTypes from '../../views/ReactBasic/child/prop-types'
import ChangeLine from '../../views/ReactBasic/child/change-line'
import PassByValue from '../../views/ReactBasic/child/pass-by-value'
import PropsChildren from '../../views/ReactBasic/child/props-children'
import Context from '../../views/ReactBasic/child/context'
import Home from '../../views/Home'
import Detail from '../../views/Detail'
import Router from '../../views/Router'
import Router1 from '../../views/Router/child/router1'
import Wirkung from '../../views/Wirkung' //效果主页
import ReactKnowledge from '../../views/Wirkung/view/p_react_knowledge' //React知识
import { react_knowledge_example } from './ReactKnowledgeDemo'
import MarkdownEdit from '../../views/Wirkung/view/p_markdown' //markdown
import Iconfont from '../../views/Wirkung/view/p_iconfont' //iconfont图标
import Classnames from '../../views/Wirkung/view/p_classnames' //classnames,多个class
import BraftEditor from '../../views/Wirkung/view/p_BraftEditor' //BraftEditor
import BraftEditorDemo1 from '../../views/Wirkung/view/p_BraftEditor/demo/demo1' //BraftEditor-demo1
import BraftEditorDemo2 from '../../views/Wirkung/view/p_BraftEditor/demo/demo2' //BraftEditor-demo2

const routes=[
  {
    path: '/',
    component: App,
    exact: true,
  },
  {
    path: '/document',
    component: Document,
  },
  {
    path: '/wirkung',
    component: Wirkung,
    children:[
      {
        path:'/wirkung/to_markdown_edit', //markdown编辑器
        component:MarkdownEdit
      },
      {
        path:'/wirkung/to_reactknowledge', //markdown编辑器
        component:ReactKnowledge,
        children:[
          { path:'/wirkung/to_reactknowledge/to_reactknowledge_demo1',component:react_knowledge_example.Demo1 },
          { path:'/wirkung/to_reactknowledge/to_reactknowledge_demo2',component:react_knowledge_example.Demo2 },
        ]
      },
      {
        path:'/wirkung/to_iconfont',//图标
        component:Iconfont
      },
      {
        path:'/wirkung/to_classnames',//多个classnames
        component:Classnames
      },
      {
        path:'/wirkung/to_brafteditor',//brafteditor
        component:BraftEditor,
        children:[
          { path:'/wirkung/to_brafteditor/to_brafteditor_demo1',component:BraftEditorDemo1 },
          { path:'/wirkung/to_brafteditor/to_brafteditor_demo2',component:BraftEditorDemo2 }
        ]
      }
    ]
  },
  {
    path: '/react-basic',
    component: ReactBasic,
  },
  {
    path: '/proptypes',
    component: PropTypes,
  },
  {
    path: '/change-line',
    component: ChangeLine,
  },
  {
    path: '/pass-by-value',
    component: PassByValue,
  },
  {
    path: '/props-children',
    component: PropsChildren,
  },
  {
    path: '/context',
    component: Context,
  },
  {
    path: '/home',
    component: Home,
  },
  {
    path: '/detail',
    component: Detail,
  },
  {
    path: '/router',
    component: Router,
  },
  {
    path: '/router1',
    component: Router1,
  },
]

const BasicRoute = () => (
  <HashRouter>
      {renderRoutes(routes)}  
  </HashRouter>
);
export default BasicRoute
/* 
npm install --save react-router-config
*/