import React from 'react';

import { Link } from 'react-router-dom'
import PropTypes from 'prop-types' //父组件传值检验类型

export default class PropType extends React.Component {
    constructor(props){
      super(props)
      this.state={
        title:'属性的类型规则(propTypes)',
      }
    }
    render() {
        return (
            <div className="PropType App_main">
                <div className="common_header">
                  <Link
                    className="App-link"
                    to="/react-basic"
                    style={{ position: "absolute", left: 0, top: "10px" }}
                  >
                    {" "}
                    &lt;--回到文档页
                  </Link>
                   <div className="common_title">{this.state.title}</div>
                </div>
                <div className="common_content">
                     <div style={{border:'1px solid #c4c4c4',paddingBottom:'20px',minHeight:'300px'}}>
                          <div className="common_code_style" style={{whiteSpace:'pre'}}>
                              {`  
                                  //安装 npm install prop-types --save 
                                  import React from 'react';
                                  import ReactDOM from 'react-dom';
                                  import PropTypes from 'prop-types'

                                  class Component1 extends React.Component{
                                      render(){
                                          return (
                                              <div>
                                                  <p>姓名：{this.props.name}</p>
                                                  <p>年龄：{this.props.age}</p>
                                                  <p>学科：</p>
                                                  <ul>
                                                  {
                                                      this.props.subjects.map(function(_item){
                                                          return <li>{_item}</li>
                                                      })
                                                  }
                                                  </ul>
                                              </div>
                                          )
                                      }
                                  }

                                  //定义属性 name 为字符串且必须有值
                                  Component1.propTypes = {
                                      name: PropTypes.string
                                  }

                                  ReactDOM.render(<Component1 name="Tom"/>, document.getElementById('div1'));

                                  prop 默认情况下是可选，常用的类型：
                                  PropTypes.array
                                  PropTypes.bool
                                  PropTypes.func
                                  PropTypes.number
                                  PropTypes.object
                                  PropTypes.string
                                  PropTypes.symbol
                                  PropTypes.any.isRequired
                              
                              `}
                          </div> 
                     </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
      document.documentElement.scrollTop=0
    }
}
// 关键代码
PropType.propTypes = {
  name: PropTypes.string
}