import React from 'react';
import MarkDown from '../../components/markdown/markdown-edit'
// import MarkDown from '@/components/markdown/index.js'
import content from './content'
export default class PMarkdown extends React.Component {
    render() {
        const markConfig={
          markConfig:{
            preview:false,     
            md:false       
          },
          markContent: content
        }
        return (
            <div>
                <h2>引入markdown</h2>
                <div>
                   <MarkDown {...markConfig}/>
                </div>
            </div>
        )
    }
}