// BraftEditor
import React from 'react';
import 'braft-editor/dist/index.css'
import BraftEditor from 'braft-editor' //引入BraftEditor编辑器
import { renderRoutes } from 'react-router-config' //引入多级路由
// const content=`
// \`\`\` txt
// 1.在阿里图标库下载图标
// 2.引入文件在项目中assets
// 3.import './assets/iconfonts/iconfont.css'
// 4.<span className="iconfont icon-gouwucheman"></span>
// \`\`\`
// `
// 文档地址1 https://www.yuque.com/braft-editor/be/gz44tn#bo49ph
// 文档地址2 https://braft.margox.cn/demos/custom
export default class PBrafteditor extends React.Component {
    constructor(props){
      super(props)
      this.state={
        editorState: BraftEditor.createEditorState('<p>Hello <b>World!</b></p>'), // 设置编辑器初始内容
      }
    }
    handleChange=()=>{

    }
    render() {
        const {location,route} = this.props;
        const { editorState } = this.state
        return (
            <div>               
                 {location.pathname==='/wirkung/to_brafteditor'?(<div>
                   <h2>引入BraftEditor富文本</h2>
                   <BraftEditor
                      value={editorState}
                      onChange={this.handleChange}
                    /></div>):(renderRoutes(route.children))}                  
            </div>
        )
    }
}